.section-news .heading{margin-bottom:30px}.news__wrapper{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;margin:0 -10px}.news__col{padding:0 10px;width:25%}@media (max-width:991px){.news__col{width:50%}}@media (max-width:600px){.news__col{width:100%}}.news__item{position:relative;display:block;max-width:290px}@media (max-width:991px){.news__item{max-width:100%;margin-bottom:20px}}.news__item:hover .news__title{color:#219653}.news__item:hover .news__image{-webkit-transform:scale(1.03);-ms-transform:scale(1.03);transform:scale(1.03)}.news__icon--video{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);width:126px;height:126px;background-image:url(/build/img/icons/play.svg);background-repeat:no-repeat;background-size:cover;z-index:20}@media (max-width:480px){.news__icon--video{width:85px;height:85px}}.news__image-wrapper{position:relative;min-height:190px;border-radius:10px;overflow:hidden;margin-bottom:20px}@media (max-width:991px){.news__image-wrapper{min-height:270px}}@media (max-width:600px){.news__image-wrapper{min-height:270px}}@media (max-width:480px){.news__image-wrapper{min-height:170px}}.news__image{position:absolute;top:0;left:0;width:100%;height:100%;-o-object-fit:cover;object-fit:cover;-webkit-transition:all .7s ease-out;-o-transition:all .7s ease-out;transition:all .7s ease-out}.news__title{font-weight:700;color:#222;-webkit-transition:all .25s ease-out;-o-transition:all .25s ease-out;transition:all .25s ease-out}.news__date,.news__title{font-size:15px;line-height:21px}.news__date{color:#707070;margin-top:10px}