.to-top{position:fixed;right:20px;bottom:20px;-webkit-transition:all .25s ease-out;-o-transition:all .25s ease-out;transition:all .25s ease-out;-webkit-transform:translateY(20px);-ms-transform:translateY(20px);transform:translateY(20px);opacity:0;visibility:hidden}.to-top--active{opacity:1;visibility:visible;-webkit-transform:translate(0);-ms-transform:translate(0);transform:translate(0)}.to-top__wrapper{height:10px;background:#000;width:100%;position:fixed;bottom:0;left:0}.to-top__wrapper .layout{position:relative}