.slider-preview{width:100%;min-width:0;display:block;max-width:720px;padding-left:20px}@media (max-width:991px){.slider-preview{width:50%}}@media (max-width:767px){.slider-preview{-webkit-box-ordinal-group:3;-ms-flex-order:2;order:2;width:100%;padding-left:0;margin-bottom:40px}}@media (max-width:600px){.slider-preview{margin-bottom:30px}}.slider-preview__init{display:block}.slider-preview__init img{width:100%}.slider-preview .slick-dots{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;margin-top:30px}@media (max-width:480px){.slider-preview .slick-dots{margin-top:10px}}.slider-preview .slick-dots li{width:15px;height:15px;background:#b8cce5;border-radius:50%;margin:0 6px;cursor:pointer;-webkit-transition:all .25s ease-out;-o-transition:all .25s ease-out;transition:all .25s ease-out}.slider-preview .slick-dots li.slick-active{width:30px;border-radius:19px;background:#7198cb}.slider-preview .slick-dots li button{display:none}