.burger-menu{position:relative;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;outline:none;border:none;height:24px;margin-left:25px;background:none;display:none}@media (max-width:991px){.burger-menu{display:-webkit-box;display:-ms-flexbox;display:flex}}.burger-menu__item{position:relative;display:inline-block;width:40px;height:3px;border-radius:10px;background:#219653;-webkit-transition:all .12s ease-out;-o-transition:all .12s ease-out;transition:all .12s ease-out}@media (max-width:480px){.burger-menu__item{width:27px}}.burger-menu__item:not(:last-child){margin-bottom:7px}.burger-icon{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;outline:none;border:none;min-height:13px;margin-left:25px;background:none}.burger-icon__item{position:relative;display:inline-block;width:17px;height:2px;background:#222;border-radius:4px;-webkit-transition:all .12s ease-out;-o-transition:all .12s ease-out;transition:all .12s ease-out}.burger-icon__item:not(:last-child){margin-bottom:4px}