:root{--white-color:#fff;--black-color:#222;--green-color:#219653;--blue-color:#7198cb}::-moz-selection{background:#ccc;color:#4f4c66}::selection{background:#ccc;color:#4f4c66}*,:after,:before{-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-tap-highlight-color:transparent}*{margin:0;padding:0}html{overflow-x:hidden}body{font-family:Ubuntu,sans-serif;font-size:16px;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-shadow:none;margin:0;min-width:320px;color:var(--black-color);background:var(--white-color);background:#f6f8f9;padding-top:140px}@media (max-width:991px){body{padding-top:80px}}a{text-decoration:none}ul{list-style:none}.all-list{position:relative}.all-list:after{position:absolute;top:100%;left:0;content:"";width:100%;height:1px;display:block;background:#222}input{outline:none}.time-item{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;font-weight:500;font-size:16px;line-height:22px;color:#7198cb}.time-item .icon-time{margin-right:10px}.link{position:relative;font-size:18px;line-height:20px;color:#219653;-webkit-transition:all .25s ease-out;-o-transition:all .25s ease-out;transition:all .25s ease-out}@media (max-width:991px){.link{text-decoration:underline}}.link:before{position:absolute;top:100%;left:0;content:"";width:100%;height:1px;background:#219653;-webkit-transition:all .25s ease-out;-o-transition:all .25s ease-out;transition:all .25s ease-out}@media (max-width:991px){.link:before{display:none}}.link:hover{color:#36d379}.link:hover:before{background:#36d379}.height-100{height:60px}@media (min-width:480px){.height-100{height:calc(60px + (4000vw - 19200px)/511)}}@media (min-width:991px){.height-100{height:100px}}.successful{position:fixed;top:10px;left:10px;z-index:1000}