.button{display:-webkit-inline-box;display:-ms-inline-flexbox;display:inline-flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;border-radius:12px;font-size:16px;text-align:center;color:#fff;background:#219653;-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out;cursor:pointer;border:none}.button--small{font-family:Roboto,sans-serif;padding:3px 16px 4px;border-radius:7px}.button--small:hover{background:#36d379}.button--small[disabled]{background:#d6d6d6}.button--big{min-height:40px;padding:10px 20px;border-radius:10px;font-weight:700;font-size:15px;line-height:21px;text-transform:uppercase;color:#fefefe}.button--big:hover{background:#36d379}.button--big[disabled]{background:#d6d6d6}.button--big-x{min-height:40px;font-weight:700;font-size:16px;line-height:22px;text-transform:none;color:#fefefe;padding:14px}.button--big-x:hover{background:#36d379}.button--big-x[disabled]{background:#d6d6d6}.button--large{min-height:30px;padding:5px 20px}.button--large:hover{background:#36d379}.button--large[disabled]{background:#d6d6d6}.button--border{background-color:rgba(0,0,0,0);font-size:14px;line-height:19px;color:#222;border:1px solid rgba(33,150,83,.5)}.button--border:hover{background-color:rgba(0,0,0,0);-webkit-box-shadow:0 0 0 2px #219653 inset;box-shadow:inset 0 0 0 2px #219653;border:1px solid #219653}.button--border[disabled]{background:#a9a9a9}.button--full-width{width:100%}.button--tab{min-width:205px}@media (min-width:991px){.button--tab{min-width:calc(205px + (6500vw - 64415px)/33)}}@media (min-width:1024px){.button--tab{min-width:270px}}@media (max-width:480px){.button--tab{width:100%}}.button .icon-telegram{margin-left:20px;fill:#fefefe}